.Contador h3 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.Contador input {
  font-size: 1.4rem;
  width: 60px;
}

.Contador button {
  margin: 15px;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-color: #1499D3;
  color: #FFF;
  font-size: 1.4rem;
}

.Contador button:focus {
  outline-style: none;
}